.products_page {
  background-color: #f0f2f5;
}
.category_name {
  text-align: center;
  font-size: 38px;
}
.all_product {
  font-size: 20px;
}

.prodcuts_container {
  background-color: #fcfcfc;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
  -webkit-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
}
.products_top_image {
  position: relative;
}
.products_image {
  width: 100%;
  min-height: 155px;
  max-height: 155px;
  position: relative;
  cursor: pointer;
}
.products_top_image:hover .product_hover_section {
  opacity: 1;
  transition: all 0.3s;
  visibility: visible;
}
.product_hover_section {
  background: rgba(0, 0, 0, 0.4);
  visibility: hidden;
  opacity: 0;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
}
.productHoverBtn:hover {
  color: #fff !important;
}

.productHoverBtn {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 14px;
  border-radius: 10px;
  color: #fff;
  outline: none;
  background: #ea6721;
  border: 0;
  padding: 5px 10px;
}
.product_brands {
  font-size: 12px;
  color: #ea6721;
  font-weight: 600;
}
.products_title {
  font-size: 16px;
}
.product_Price {
  font-weight: 600;
}
.products_btn {
  font-size: 14px;
  border-radius: 10px;
  background: #f26f00;
  color: #fff;
  outline: none;
  border: 0;
  padding: 5px 10px;
}
.products_btn:hover {
  background: #1a1823 !important;
  color: #fff;
}
.position {
  position: inherit;
}

@media (max-width: 990px) and (min-width: 540px) {
  .carouselcol.col {
    padding: 7px !important;
  }
}
@media (max-width: 400px) {
  .productHoverBtn{
    font-size: 12px;
    padding: 4px 4px;
  }
  .products_btn  {
    font-size: 12px;
  }
}
